// Import React Component
import React from "react";

// Import Styles
import "./SingleProductItem.style.scss";

// Import Card Component
import CardItem from "./CardItem";

// Import Reactstrap Components
import { Container, Row } from "reactstrap";

// Import Translation Module
import { useTranslation } from "react-i18next";

// Header Background Image
import headerBackgroundImage from "./singleProductItemHeaderBackground.jpg";

// Card Items Images
import cardItem1 from "./signleProductItemCardItem1.jpg";
import cardItem2 from "./signleProductItemCardItem2.jpg";
import cardItem3 from "./signleProductItemCardItem3.jpg";
import cardItem4 from "./signleProductItemCardItem4.jpg";
import cardItem5 from "./signleProductItemCardItem5.jpg";
import cardItem6 from "./signleProductItemCardItem6.jpg";

function SingleProductItem() {
  const { t } = useTranslation();

  return (
    <section id="single-product-item" class="single-product-item">
      <header>
        <img src={headerBackgroundImage} alt="Header Background Image" />
        <Container>
          <div className="single-product-item__heading">
            <h1>Package main systems</h1>
          </div>
        </Container>
      </header>
      <main>
        <article className="single-product-item__content">
          <Container>
            <section id="gen2">
              <h2>{t("Gen2")}</h2>
              <Row className="d-flex align-items-start justify-content-start flex-wrap mb-5">
                <CardItem cardImage={cardItem1} title="dumb-waiter" />
                <CardItem cardImage={cardItem2} title="bed lift" />
                <CardItem cardImage={cardItem3} title="parkink lift" />
                <CardItem cardImage={cardItem4} title="parkink lift" />
                <CardItem cardImage={cardItem5} title="panoramic lift" />
                <CardItem cardImage={cardItem6} title="load lift" />
              </Row>
            </section>
            <section id="skyrise">
              <h2>{t("Skyrise")}</h2>
              <Row className="d-flex align-items-start justify-content-start flex-wrap mb-5">
                <CardItem cardImage={cardItem1} title="dumb-waiter" />
                <CardItem cardImage={cardItem2} title="bed lift" />
                <CardItem cardImage={cardItem3} title="parkink lift" />
                <CardItem cardImage={cardItem4} title="parkink lift" />
                <CardItem cardImage={cardItem5} title="panoramic lift" />
                <CardItem cardImage={cardItem6} title="load lift" />
              </Row>
            </section>
            <section id="skybuild">
              <h2>{t("Skybuild")}</h2>
              <Row className="d-flex align-items-start justify-content-start flex-wrap mb-5">
                <CardItem cardImage={cardItem1} title="dumb-waiter" />
                <CardItem cardImage={cardItem2} title="bed lift" />
                <CardItem cardImage={cardItem3} title="parkink lift" />
                <CardItem cardImage={cardItem4} title="parkink lift" />
                <CardItem cardImage={cardItem5} title="panoramic lift" />
                <CardItem cardImage={cardItem6} title="load lift" />
              </Row>
            </section>
          </Container>
        </article>
      </main>
    </section>
  );
}

export default SingleProductItem;
