// Import React Component
import React from "react";

// Import Translation Module
import { useTranslation } from "react-i18next";

// Import Reactstrap Components
import { Col } from "reactstrap";

function CardItem({ title, cardImage }) {
  const { t } = useTranslation();

  return (
    <Col lg="3" col="12" md="6" className="single-product-item__content__item">
      <div className="single-product-item__content__item-content-wrap">
        <div className="single-product-item__content__item__heading">
          <h3>{t(`${title}`)}</h3>
        </div>
        <div className="single-product-item__content__item__image">
          <img src={cardImage} alt={t(`${title}`)} />
        </div>
      </div>
    </Col>
  );
}

export default CardItem;
